<template>
  <div class="trialpreson">

    <!-- 数值形 1 -->
    <el-table v-if="resultType==1"
      stripe
      ref="multipleTable"
      :data="smallProjectData"
      tooltip-effect="dark"
      header-cell-class-name="custom-table-cell"
      key='1'>
      <el-table-column align="center"
        label="小项名称"
        show-overflow-tooltip>
        <template slot-scope="scope">
          {{scope.row.pname||scope.row.projectName}}
        </template>
      </el-table-column>
      <el-table-column align="center"
        label="结果">
        <template slot-scope="scope">
          <el-input placeholder="请输入"
            size='small'
            v-model="scope.row.checkValue"
            @change="checkEvent($event, scope.row)"
            @keyup.native.enter="handleEnter(scope.$index, scope.row.checkValue, 'vIpt-1-')"
            :ref='"vIpt-1-" + scope.$index'
            tabindex='1'></el-input>
        </template>
      </el-table-column>
      <el-table-column align="center"
        label="单位"
        show-overflow-tooltip>
        <template slot-scope="scope">
          {{scope.row.projectSymbol}}
        </template>
      </el-table-column>
      <el-table-column align="center"
        prop="rule"
        label="规则"
        show-overflow-tooltip></el-table-column>
      <el-table-column width="55"
        label="阳性"
        show-overflow-tooltip>
        <template slot-scope="scope">
          <el-checkbox class="green"
            v-model="scope.row.nature"
            true-label="2"
            false-label="1" />
        </template>
      </el-table-column>
    </el-table>
    <div class="checkmain"
      v-if="resultType==1"
      key='1-1'>
      <div class="checkmain">检查结论：</div>
      <div class="jlmain">
        <div class="jlcentent">
          <div class="flex"
            v-for="(item, index) in conclusionslist"
            :key="index">
            <div class="project-label">{{item.pname||item.projectName}}：</div>
            <div class="flex-item">
              <el-input type="textarea"
                ref='textarea1'
                @change="isIO=true"
                v-model="item.checkConclusion"
                :autosize="{ minRows: 1 }"
                resize='none'
                style="width:100%"
                tabindex='1' />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="checkmain"
      v-if="resultType==1"
      key='1-2'>
      <div class="checkmaintitle">诊断意见或印象：</div>
      <div class="jlmain">
        <div class="jlcentent">
          <div class="flex"
            v-for="(item,index) in conclusionslist"
            :key="index">
            <div class="project-label">{{item.pname||item.projectName}}：</div>
            <div class="flex-item">
              <el-input type="textarea"
                ref='textarea2'
                v-model="item.checkProposal"
                @change="isIO=true"
                :autosize="{ minRows: 1 }"
                resize='none'
                style="width:100%"
                tabindex='1' />
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 文本型(医技) 2 -->
    <el-form size='small'
      label-width="auto"
      class="default-form-style"
      v-if="resultType==2"
      style="overflow:hidden"
      key='2-1'>
      <el-row v-for="item in smallProjectData"
        :key="item.projectId">
        <el-col :span="12">
          <el-form-item label='项目名称：'>
            {{item.pname}}
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label='检查部位：'>
            <el-input v-model="item.checkPart"
              placeholder="请输入"></el-input>
          </el-form-item>
        </el-col>
      </el-row>

    </el-form>
    <div class="checkmain"
      v-if="resultType==2"
      key='2-2'>
      <div class="checkmain">检查结论：</div>
      <div class="jlmain"
        @click='addList'>
        <div class="flex"
          v-for="(t, i) in conclusionslist"
          :key='i'>
          <label class="project-label"
            v-show='conclusionslist.length>1'>
            {{t.pname || t.projectName}}：
          </label>
          <el-input type="textarea"
            ref='textarea1'
            class="flex-item"
            v-model="t.checkConclusion"
            @change="checkEvent($event, t)"
            :autosize='{minRows: 1}'
            :autofocus='0===i'
            resize='none'
            @click.native.stop />
        </div>
      </div>
    </div>
    <div class="checkmain"
      v-if="resultType==2"
      key='2-3'>
      <div class="checkmaintitle">诊断意见或印象：</div>
      <div class="jlmain"
        @click="$nextTick(() => $refs['textarea2'][0].focus())">
        <div class="flex"
          v-for="(t, i) in conclusionslist"
          :key='i'>
          <label class="project-label"
            v-show='conclusionslist.length>1'>
            {{t.pname || t.projectName}}：
          </label>
          <el-input type="textarea"
            ref='textarea2'
            class="flex-item"
            v-model="t.checkProposal"
            @change="isIO=true"
            :autosize='{minRows: 1}'
            @click.native.stop
            resize='none' />
        </div>
      </div>
    </div>

    <!-- 文本型(科室) 3 -->
    <el-table v-if="resultType==3"
      stripe
      ref="multipleTable"
      :data="smallProjectData"
      tooltip-effect="dark"
      header-cell-class-name="custom-table-cell"
      key="3">
      <el-table-column align="center"
        label="小项名称"
        prop="name"
        width="200"
        show-overflow-tooltip>
        <template slot-scope="scope">
          {{scope.row.pname||scope.row.projectName}}
        </template>
      </el-table-column>
      <el-table-column align="center"
        label="结果">
        <template slot-scope="scope">
          <el-input class="inputjieguo"
            placeholder="请输入"
            v-model="scope.row.checkValue"
            @change="checkEvent($event, scope.row)"
            @keyup.native.enter="handleEnter(scope.$index, scope.row.checkValue, 'vIpt-3-')"
            :ref='"vIpt-3-" + scope.$index'
            size="small"
            tabindex='1'></el-input>
        </template>
      </el-table-column>
      <el-table-column width="80"
        label="阳性"
        show-overflow-tooltip>
        <template slot-scope="scope">
          <el-checkbox class="green"
            v-model="scope.row.nature"
            true-label="2"
            false-label="1" />
        </template>
      </el-table-column>
    </el-table>
    <div class="checkmain"
      v-if="resultType==3"
      key="3-1">
      <div class="checkmain">检查结论：</div>
      <div class="jlmain">
        <div class="jlcentent">
          <div class="flex"
            v-for="(item,index) in conclusionslist"
            :key="index">
            <div class="project-label">{{item.pname||item.projectName}}：</div>
            <div class="flex-item">
              <el-input type="textarea"
                ref="textarea1"
                v-model="item.checkConclusion"
                @change="isIO=true"
                :autosize="{ minRows: 1 }"
                resize='none'
                style="width:100%"
                tabindex='1' />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="checkmain"
      v-if="resultType==3"
      key="3-2">
      <div class="checkmaintitle">诊断意见或印象：</div>
      <div class="jlmain">
        <div class="jlcentent">
          <div class="flex"
            v-for="(item,index) in conclusionslist"
            :key="index">
            <div class="project-label">{{item.pname || item.projectName}}：</div>
            <div class="flex-item">
              <el-input type="textarea"
                ref="textarea2"
                v-model="item.checkProposal"
                @change="isIO=true"
                :autosize="{ minRows: 1 }"
                resize='none'
                style="width:100%"
                tabindex='1' />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'trialpresonprefected',
  props: {
    smallProjectData: {
      type: Array,
      default() {
        return []
      }
    },
    resultType: {
      type: String,
      default: ''
    },
    editType: {
      type: Number,
      default: 0
    },
    maxIndex: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      multipleSelection: [],
      conclusionslist: [],
      isShow: '', //
      isIO: false // 用户是否有输入值修改信息，用于离开提示用户保存
    }
  },

  methods: {
    // 结果类型为2 点击检查结论框
    addList() {
      if (this.conclusionslist.length) {
        this.$refs['textarea1'][0].focus()
        return
      }
      let originData = JSON.parse(JSON.stringify(this.smallProjectData))
      this.conclusionslist = originData.map(item => {
        item.checkConclusion = item.checkConclusion || ''
        item.checkProposal = item.checkProposal || ''
        return item
      })

      this.$nextTick(() => this.$refs['textarea1'][0].focus())
    },

    // 根据输入的值进行查询
    checkEvent(inputValue, row) {
      this.isChange = true
      if (inputValue.trim() === '') return
      let url = '/sys/projectItemResult/getProjectResult'
      let { projectId, resultType } = row
      this.$post(url, { projectId, resultType, inputValue })
        .then(({ data }) => {
          if (resultType == 2) {
            row.checkProposal = data.checkProposal
            row.diseaseType = data.diseaseType
            row.checkWaring = data.checkWaring
          } else {
            data.checkValue = inputValue
            data.pname = row.pname || row.projectName
            let i = this.conclusionslist.findIndex(item => item.projectId === data.projectId)
            i > -1 ? this.conclusionslist.splice(i, 1, data) : this.conclusionslist.push(data)
          }
          this.isChange = false
          this.isSubmit && this.$nextTick(() => this.$refs.textarea1[0].focus())
          // this.$emit('submitSave')
        })
        .catch(error => {
          this.$message.error(error)
        })
      this.isIO = true
    },

    // 处理回车
    handleEnter(i, v, k) {
      this.isSubmit = false
      if (!v.trim()) return
      let l = this.smallProjectData.length

      // 检测前面输入框的值
      for (let j = 0; j < i; j++) {
        if (0 === i) continue
        let $ipt = this.$refs[k + j]
        if (!$ipt.value.trim()) return $ipt.focus()
      }
      if (i === l - 1) {
        return this.isChange
          ? (this.isSubmit = true)
          : this.$nextTick(() => this.$refs.textarea1[0].focus())
        // this.$emit('submitSave')
      }
      this.$refs[k + (i + 1)].focus()
    }
  },

  destroyed() {
    sessionStorage.setItem(this.maxIndex, JSON.stringify(this.conclusionslist))
  },

  watch: {
    maxIndex: {
      handler(val, old) {
        this.isIO = false
        if (old) this[old] = this.conclusionslist
        let data = JSON.parse(sessionStorage.getItem(val)) || []
        this.conclusionslist = this.editType
          ? JSON.parse(JSON.stringify(this.smallProjectData))
          : this[val]
          ? this[val]
          : data
        sessionStorage.removeItem(val)
      },
      immediate: true
    }
  }
}
</script>

