</template>
<script>
export default {
  name: 'trialpresonprefected',
  props: {
    smallProjectData: {
      type: Array,
      default() {
        return []
      }
    },
    resultType: {
      type: String,
      default: ''
    },
    maxIndex: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      conclusionslist: [] //检查结果
    }
  },

  watch: {
    maxIndex: {
      handler(val) {
        this.conclusionslist = this.smallProjectData
      },
      immediate: true
    }
  }
}
</script>

