<template>
  <div class="trialpreson flex-v page">
    <el-breadcrumb separator-class="el-icon-arrow-right"
      class="my-breadcrumb headertitle">
      <el-breadcrumb-item :to="{ name: 'FirstTrial' }">初审台</el-breadcrumb-item>
      <el-breadcrumb-item>初审人员</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="tabledata">
      <div class="leftmeun">
        <div class="topmeun">
          <el-menu :default-active="defaultactive"
            class="el-menu-vertical-demo">
            <el-submenu :index="''+index"
              v-for="(item, index) in totalData"
              :key="index">
              <template slot="title">
                <i class="icon iconfont bj-keshiIcon"
                  :class="{'active':keshiindex===index}">
                  <span class="reddian"
                    v-show="!item.isAllCheck"></span>
                </i>
                <span>{{item.dname}}</span>
              </template>
              <el-menu-item-group>
                <el-menu-item class="menuitem"
                  @click.native="clickmenuevent(index, count, items.sysGuid, items.resultType, items.isAllCheck)"
                  :index="`${index}-${count}`"
                  v-for="(items, count) in item.bigProject"
                  :key="count">
                  <span style="margin:0 10px 0 -24px;color:red;visibility: hidden;"
                    :style="{visibility:items.isAllCheck ? 'hidden' : 'visible'}">●</span>
                  <span slot='title'>{{items.pname}}</span>
                </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </div>

        <div class="leftmeunfoot">
          <div class="huicolor">注：红色为未审核项</div>
        </div>
      </div>
      <div class="rightconcent flex-v">
        <div class="toppresonxinxi">
          <div class="presonIcon flex-v-center">
            <img v-if="ponserData.addOn1"
              :src="ponserData.addOn1"
              alt=''>
            <template v-else>
              <img v-if="ponserData.sexType==2"
                src="@/assets/img/girlbackImg.png" />
              <img v-else
                src="@/assets/img/boxbackImg.png" />
            </template>
          </div>
          <div class="presoncontent flex-item">
            <div class="cell-item-list">
              <div class="cell-item">
                <label class="lable">姓名 : </label>
                <template class="template">{{ponserData.userName}}</template>
              </div>
              <div class="cell-item">
                <label class="lable">性别 : </label>
                {{
                  ponserData.sexType == 1 ? '男' :
                  ponserData.sexType == 2 ? '女' : '不详'
                }}
              </div>
              <div class="cell-item">
                <label class="lable">年龄 : </label>
                <template class="template">{{ponserData.age}}</template>
              </div>
              <div class="cell-item">
                <label class="lable">居民类型 : </label>
                <template class="template">{{ponserData.personType | getBoxType}}</template>
              </div>
              <div class="cell-item">
                <label class="lable">既往史 : </label>
                <template class="template">{{historyDisease | emptyData}}</template>
              </div>
              <div class="cell-item redcolor"
                v-if="ponserData.examStatus==4">
                <label>驳回原因 : </label>
                <template class="template ">{{ponserData.backReason}}</template>
              </div>
              <div class="cell-item bottombtngrop flex"
                style="width:0"
                v-if="isShowExamBtn">
                <el-button size="small"
                  class="btn-info"
                  @click="audit">审核</el-button>
              </div>
            </div>
          </div>
        </div>
        <div class="presoncontent-table table-box">
          <component :is="currentTabComponent"
            :smallProjectData='totlesmalldata'
            :resultType='resultType'
            :editType='editType'
            :maxIndex='projectIndex'
            ref="conclusionslist"
            :key="currentTabComponent"
            @submitSave='submitSave'>
          </component>
        </div>
      </div>
    </div>
    <div class="footer">
      <div class="bottombtngrop flex"
        style="justify-content: center;">
        <div class="csdoctor flex"
          style="margin-right: 90px;">
          <span class="lableitem">初审医生：</span>
          {{(ponserData.examStatus > 1 && ponserData.oneAuthUserName) | emptyData}}
        </div>
        <div class="csdate flex">
          <span class="lableitem">初审日期：</span>
          {{(ponserData.examStatus > 1 && ponserData.updateDate) | formatDate}}
        </div>
      </div>
      <div class="bottombtngrop flex"
        style="justify-content:flex-end;">
        <el-button size="small"
          class="btn-red"
          v-if="checkstatus==1"
          @click="editevent">
          修改
        </el-button>
        <el-button size="small"
          class="btn-info"
          v-if="checkstatus==0"
          :loading="saveLoading"
          @click="submitSave">
          保存
        </el-button>
        <el-button class="btn-red"
          v-if="checkstatus==0"
          @click="syncData">
          同步数据
        </el-button>
      </div>
    </div>
  </div>
</template>


<script>
import trialedit from '@/components/trialperson/trialedit'
import trialprefected from '@/components/trialperson/trialprefected'
import { calcType } from '@/lib/utils'

export default {
  name: 'trialpresonprefected',
  components: {
    trialedit,
    trialprefected
  },
  data() {
    return {
      checkstatus: '', //判断一开始是否显示详情还是添加修改界面
      resultType: '', //标识是数值型还是文本型
      editType: 0, //是否是修改状态
      currentTabComponent: 'trialprefected', // 默认为已完成组件，完成组件只展示信息，逻辑没有新增和修改复杂
      examUserId: '', //体检流水号
      centerDialogtbdata: false,
      ponserData: {},
      departmentData: '', //科室列表
      sportsData: [], //大项列表
      smallProjectData: [],
      totlesmalldata: [], //对应的小项
      defaultactive: '0-0', //当前项的索引
      projectIndex: '0-0',
      keshiindex: 0, //科室的索引
      count: 0, //大项索引
      totalData: [], //大项和小项的结合
      saveLoading: false
    }
  },

  computed: {
    // 计算既往史
    historyDisease() {
      if (!Object.keys(this.ponserData).length) return ''
      let aTemp = []
      ;[
        'historyIllnessJson',
        'historyOperationJson',
        'historyTransfusionJson',
        'historyTraumaJson'
      ].forEach(item => {
        try {
          let arr = JSON.parse(this.ponserData[item])
          aTemp = aTemp.concat(arr.map(t => t.name))
        } catch (err) {}
      })
      return aTemp.join('/')
    },

    // 是否显示审核按钮
    isShowExamBtn() {
      if (!this.totalData.length) return false
      let examStatus = this.ponserData.examStatus
      let waitExam = examStatus == 1 || examStatus == 4
      let isAllCheck = this.totalData.every(item => item.isAllCheck)
      return waitExam && isAllCheck && this.currentTabComponent === 'trialprefected'
    }
  },

  filters: {
    getBoxType: function(val) {
      let { nameList } = calcType(val)
      return nameList ? nameList.join('/') : '未分配'
    }
  },

  created() {
    this.examUserId = this.$route.query.examUserId
    this.checkstatus = this.$route.query.status
    this.getData()
  },

  destroyed() {
    // 暂时全部清空
    sessionStorage.clear()
  },

  methods: {
    //获取数据
    getData() {
      let url = '/sys/project/getShowExam'
      let params = { sysGuid: this.examUserId }
      this.$post(url, params)
        .then(({ data }) => {
          this.ponserData = data.user
          let { depts, bigProject, smallProject } = data
          this.smallProjectData = smallProject
          this.totalData = depts.map(item => {
            let o = {
              dname: item.dname,
              sysGuid: item.sysGuid,
              bigProject: []
            }
            bigProject.forEach(items => {
              if (item.sysGuid === items.deptId) {
                o.bigProject.push(items)

                // 检测大项中的所有小项是否全部都已检查
                let isAllCheck = (function() {
                  for (let i = 0, t; (t = smallProject[i++]); ) {
                    if (items.sysGuid === t.parentId) {
                      if (t.isCheck != 1) return false
                    }
                  }
                  return true
                })()
                items.isAllCheck = isAllCheck
              }
            })
            o.isAllCheck = o.bigProject.every(t => t.isAllCheck)
            return o
          })
          let firstBigProject = this.totalData[0].bigProject[0]
          this.resultType = firstBigProject.resultType
          this.getsportsData(firstBigProject.sysGuid, firstBigProject.isAllCheck)
        })
        .catch(error => {
          error && this.$message.error(error)
        })
    },

    // 根据大项找小项
    getsportsData(sysGuid, isAllCheck) {
      this.totlesmalldata = []
      this.smallProjectData.forEach((item, index) => {
        if (sysGuid == item.parentId) {
          this.totlesmalldata.push(item)
        }
      })
      if (isAllCheck) {
        this.checkstatus = 1
        this.currentTabComponent = 'trialprefected'
      } else {
        this.checkstatus = 0
        this.editType = 0
        this.currentTabComponent = 'trialedit'
      }
    },

    // 点击侧边栏子菜单
    clickmenuevent(index, count, sysGuid, resultType, isAllCheck) {
      this.defaultactive = `${index}-${count}`
      this.projectIndex = `${index}-${count}`
      this.resultType = resultType
      this.keshiindex = index
      this.count = count
      this.getsportsData(sysGuid, isAllCheck)
    },

    syncData() {
      this.$router.push({
        path: '/Synchronousdata',
        query: {}
      })
    },

    editevent() {
      this.checkstatus = 0
      this.editType = 1
      this.currentTabComponent = 'trialedit'
    },

    // 提交保存
    submitSave() {
      this.totlesmalldata.forEach(item => {
        item.isCheck = '1'
        item.flow = '1'
        this.$refs.conclusionslist.conclusionslist.forEach(el => {
          if (item.projectId == el.projectId) {
            item.checkConclusion = el.checkConclusion
            item.checkProposal = el.checkProposal
            item.diseaseType = el.diseaseType
            item.checkResult = el.checkProposal
            item.checkWaring = el.checkWaring
          }
        })
      })
      let url = '/dev/examUserProjectItem/addExamData'
      this.saveLoading = true
      this.$post(url, this.totlesmalldata, 'json')
        .then(res => {
          this.saveLoading = false
          this.currentTabComponent = 'trialprefected'
          this.checkstatus = 1

          // 检查是否全部检查，以取消红点显示
          let dept = this.totalData[this.keshiindex] // 当前科室
          let maxProject = dept.bigProject[this.count] // 当前大项
          maxProject.isAllCheck = true
          dept.isAllCheck = dept.bigProject.every(item => item.isAllCheck)
          this.totalData.splice(this.keshiindex, 1, dept)
        })
        .catch(error => {
          this.saveLoading = false
          this.$message.error(error)
        })
    },

    // 审核
    audit() {
      if (!this.totalData.every(t => t.isAllCheck)) return this.$message.warning('还有未检项目！')
      this.$confirm('', {
        title: '确定要审核？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(e => {
          let url = '/dev/examUserProjectItem/getCheck'
          const params = { examUserId: this.examUserId }
          this.$post(url, params)
            .then(res => {
              this.$message.success(res.data)
              this.$router.push({
                path: '/FirstTrial'
              })
            })
            .catch(error => {
              this.$message.error(error)
            })
        })
        .catch(err => err)
    }
  }
}
</script>

