      color: #666666;
    }
  }
  //  .footer{
  .bottombtngrop {
    width: 50%;
    //  display:flex;
    align-items: center;
    .csdoctor {
      .lableitem {
        color: #333333;
      }
      font-size: 14px;
      color: #666666;
    }
    .csdate {
      .lableitem {
        color: #333333;
      }
      font-size: 14px;
      color: #666666;
    }
    .btn {
      cursor: pointer;
      width: 101px;
      height: 32px;
      line-height: 32px;
      font-size: 14px;
      border-radius: 4px;
      text-align: center;
      margin-left: 10px;
      border: 0 none;
    }
    .qxbtn {
      color: #07caa6;
      border: solid 1px #00d0a6;
    }
    .bcbtn {
      background-color: #00d0a6;
