  }
  .keshimain {
    //  height:400px;
    .keshilist {
      width: 320px;
      margin: 0 auto;
      .keshititleitem {
        margin-top: 20px;
        display: flex;
        align-items: center;
        .keshiicon {
          min-width: 36px;
          height: 36px;
          background: #ccc;
          border-radius: 50%;
          margin-right: 10px;
          display: flex;
        }
        .keshititle {
